import { useCallback, useEffect, useState } from 'react';
import { useProduct } from '../../../hooks/product/useProduct';
import { ComponentStyle as S } from './CartListItem.styled';
import { ProductProps } from '../../../types/product';
import { theme } from '../../../styles/theme';
import CommonButton from '../../common/motion/CommonButton/CommonButton';
import { useRouter } from '../../../hooks/useRouter';
import { ROUTE_PATH } from '../../../constants/path';
import { getDiscount } from '../../../utils/getDiscount';
import IconCheckbox2 from '../../icons/IconCheckbox2';

interface CartListItemProps {
  productId: string;
  isChecked: boolean;
  onToggleCheck: () => void;
  onCountChange: (count: number) => void;
  count: number;
  options: Record<string, string>;
  onRemoveItem: () => void;
}

const CartListItem = ({
  productId,
  options,
  onRemoveItem,
  count,
  onCountChange,
  isChecked,
  onToggleCheck,
}: CartListItemProps) => {
  /**
   * ì¥ë°”êµ¬ë‹ˆ í˜ì´ì§€ì—ì„œ ë‚˜ì—´ë˜ëŠ” ìƒí’ˆ í•˜ë‚˜ë¥¼ ë‹´ë‹¹í•©ë‹ˆë‹¤.
   * ìƒí’ˆì˜ ë¸Œëœë“œ, ì´ë¦„, ì¸ë„¤ì¼, ì„ íƒëœ ì˜µì…˜ì„ ë³´ì—¬ì£¼ê³ ,
   * ìƒí’ˆì„ ì„ íƒ/ì·¨ì†Œ, ì‚­ì œ, ê°œìˆ˜ë³€ê²½ì˜ ê¸°ëŠ¥ì„ ê°€ì§€ê³  ìˆìŠµë‹ˆë‹¤.
   */

  const [product, setProduct] = useState<ProductProps | null>(null);
  const { productQuery, getProduct } = useProduct();

  const { navigate } = useRouter();

  useEffect(() => {
    setProduct(getProduct(productId));
  }, [getProduct, productId]);

  const handleClickRemove = useCallback(() => {
    onRemoveItem();
  }, [onRemoveItem]);

  const handleNavigateProductPage = useCallback(() => {
    navigate(ROUTE_PATH.productDetail.replace(':id', productId));
  }, [navigate, productId]);

  const handleClickBrand = useCallback(() => {
    const brandId = product?.brandId ? product?.brandId : '';
    navigate(ROUTE_PATH.brandDetail.replace(':id', `${brandId}`));
  }, [navigate, product]);

  const handleIncreaseCount = useCallback(() => {
    if (count < 999) {
      onCountChange(++count);
    }
  }, [count, onCountChange]);

  const handleDecreaseCount = useCallback(() => {
    if (count > 1) {
      onCountChange(--count);
    }
  }, [count, onCountChange]);

  if (!productQuery.isSuccess) return <></>;
  return (
    <S.Component>
      {/* í—¤ë”: ìƒí’ˆ ì²´í¬ë°•ìŠ¤ì™€ ì‚­ì œ ë²„íŠ¼ */}
      <S.Header>
        <S.Checkbox onClick={onToggleCheck}>
          <IconCheckbox2 checkedColor={theme.color.SIGNATURE} isChecked={isChecked} />
        </S.Checkbox>
        <CommonButton>
          <S.DeleteButton onClick={handleClickRemove}>
            <p>ì‚­ì œ</p>
          </S.DeleteButton>
        </CommonButton>
      </S.Header>

      {/* ë°”ë””: ì¸ë„¤ì¼ ì´ë¯¸ì§€ì™€ ë¸Œëœë“œ, ìƒí’ˆ ì´ë¦„, ì„ íƒí•œ ì˜µì…˜, ìƒí’ˆ ê°€ê²©ê³¼ í• ì¸ ì •ë³´ */}
      <S.Body>
        <CommonButton>
          <S.Thumb
            onClick={handleNavigateProductPage}
            style={{ backgroundImage: `url('${product?.thumb[0]}')` }}
          ></S.Thumb>
        </CommonButton>

        <S.Info>
          <CommonButton>
            <S.Brand onClick={handleClickBrand}>{product?.brandName}</S.Brand>
          </CommonButton>
          <CommonButton>
            <S.Name onClick={handleNavigateProductPage}>{product?.name}</S.Name>
          </CommonButton>
          <S.Options>
            {Object.keys(options).map((key) => (
              <span key={key}>
                {key} {options[key]}
              </span>
            ))}
          </S.Options>
          <S.SinglePrice>
            <S.OriginalSinglePrice>{product?.price.toLocaleString()}ì›</S.OriginalSinglePrice>
            <S.SinglePriceDiscount>
              [{getDiscount(product?.price, product?.discount)}%] {product?.discount}ì›
            </S.SinglePriceDiscount>
          </S.SinglePrice>
        </S.Info>
      </S.Body>

      {/* í‘¸í„°: ìµœì¢… ê°€ê²©ê³¼ ìˆ˜ëŸ‰ ì˜µì…˜ */}
      <S.Footer>
        <S.Count>
          <CommonButton>
            <S.DecreaseButton onClick={handleDecreaseCount} />
          </CommonButton>
          <S.CountValue>
            <p>{count}</p>
          </S.CountValue>
          <CommonButton>
            <S.IncreaseButton onClick={handleIncreaseCount} />
          </CommonButton>
        </S.Count>

        <S.FinalPriceWrap>
          <S.FinalPrice>
            ì´ {product?.discount !== undefined ? (product?.discount * count).toLocaleString() : ''}
          </S.FinalPrice>
          <S.PriceUnit>ì›</S.PriceUnit>
        </S.FinalPriceWrap>
      </S.Footer>
    </S.Component>
  );
};

export default CartListItem;
